<template>
  <div 
    class="home"
    @dragover.prevent
    @drop.prevent="handleDrop"
  >
    <div class="content-wrapper">
      <header class="header">
        <div class="header-left">
          <h1>宁猫的图片管理</h1>
          <div class="search-box">
            <i class="fas fa-search search-icon"></i>
            <input 
              type="text" 
              v-model="searchQuery" 
              placeholder="搜索图片..."
              class="search-input"
            >
          </div>
        </div>
        
        <div class="header-right">
          <div class="category-section">
            <select v-model="currentCategory" class="category-select">
              <option value="">全部分类</option>
              <option v-for="category in allCategories" :key="category" :value="category">
                {{ category }}
              </option>
            </select>
            <button class="category-manage-btn" @click="showCategoryModal = true">
              <i class="fas fa-cog"></i>
              管理分类
            </button>
            <button class="add-category-btn" @click="showAddCategory = true">
              <i class="fas fa-plus"></i>
              添加分类
            </button>
          </div>
          
          <div class="upload-section">
            <input 
              type="file" 
              ref="fileInput"
              @change="handleFileSelect"
              accept="image/*"
              multiple
              style="display: none"
            >
            <button class="upload-btn" @click="triggerUpload">
              <i class="fas fa-cloud-upload-alt"></i>
              <span v-if="!uploading">上传图片</span>
              <span v-else>{{ uploadProgress }}</span>
            </button>
          </div>
        </div>
      </header>

      <div class="stats-bar">
        <div class="stat-item">
          <i class="fas fa-images"></i>
          <span>总计: {{ images.length }} 张图片</span>
        </div>
        <div class="stat-item">
          <i class="fas fa-folder"></i>
          <span>分类: {{ allCategories.length }} 个</span>
        </div>
        <div class="stat-item">
          <i class="fas fa-filter"></i>
          <span>当前显示: {{ filteredImages.length }} 张</span>
        </div>
      </div>

      <!-- 添加分类的弹窗 -->
      <div v-if="showAddCategory" class="modal-overlay">
        <div class="modal">
          <h3>添加新分类</h3>
          <input 
            v-model="newCategory" 
            type="text" 
            placeholder="请输入分类名称"
            class="category-input"
            @keyup.enter="addCategory"
          >
          <div class="modal-buttons">
            <button @click="addCategory" class="confirm-btn">确定</button>
            <button @click="showAddCategory = false" class="cancel-btn">取消</button>
          </div>
        </div>
      </div>

      <!-- 上传图片时选择分类的弹窗 -->
      <div v-if="showUploadModal" class="modal-overlay">
        <div class="modal upload-modal">
          <h3>上传图片</h3>
          <div class="upload-form">
            <div class="form-group">
              <label>选择分类：</label>
              <select v-model="selectedCategory" class="category-select">
                <option value="">请选择分类</option>
                <option v-for="category in allCategories" :key="category" :value="category">
                  {{ category }}
                </option>
              </select>
            </div>
            <div class="selected-files">
              <div class="files-info">
                <i class="fas fa-images"></i>
                <span>已选择 {{ selectedFiles.length }} 个文件</span>
              </div>
              <div class="upload-progress" v-if="uploading">
                <div class="progress-bar">
                  <div class="progress" :style="{ width: uploadPercentage + '%' }"></div>
                </div>
                <span class="progress-text">{{ uploadProgress }}</span>
              </div>
            </div>
          </div>
          <div class="modal-buttons">
            <button 
              @click="confirmUpload" 
              class="confirm-btn"
              :disabled="uploading || !selectedCategory"
            >
              <i class="fas fa-cloud-upload-alt" v-if="!uploading"></i>
              <i class="fas fa-spinner fa-spin" v-else></i>
              {{ uploading ? uploadProgress : '确认上传' }}
            </button>
            <button @click="cancelUpload" class="cancel-btn" :disabled="uploading">
              取消
            </button>
          </div>
        </div>
      </div>

      <!-- 添加批量操作工具栏 -->
      <div v-if="selectedImages.length" class="batch-toolbar">
        <div class="selected-count">
          已选择 {{ selectedImages.length }} 张图片
        </div>
        <div class="batch-actions">
          <button @click="showBatchMoveModal = true" class="batch-btn">
            <i class="fas fa-folder-open"></i>
            移动到
          </button>
          <button @click="batchDownload" class="batch-btn" :disabled="downloading">
            <i class="fas" :class="downloading ? 'fa-spinner fa-spin' : 'fa-download'"></i>
            {{ downloading ? '准备下载...' : '下载' }}
          </button>
          <button @click="confirmBatchDelete" class="batch-btn delete">
            <i class="fas fa-trash-alt"></i>
            删除
          </button>
          <button @click="clearSelection" class="batch-btn">
            <i class="fas fa-times"></i>
            取消选择
          </button>
        </div>
      </div>

      <div class="image-grid">
        <div v-if="loading && !images.length" class="loading">
          <div class="loading-spinner">
            <div class="spinner"></div>
            <span>加载中...</span>
          </div>
        </div>
        <div v-else-if="filteredImages.length === 0" class="no-images">
          <i class="fas fa-image"></i>
          <p>没有找到匹配的图片~</p>
        </div>
        <div v-else class="grid">
          <div v-for="image in filteredImages" :key="image.name" class="image-card" 
               :class="{ 'selected': isSelected(image) }"
               @click.ctrl="toggleSelect(image)"
          >
            <div class="select-overlay" v-if="isSelected(image)" @click.stop="toggleSelect(image)">
              <i class="fas fa-check-circle"></i>
            </div>
            <div class="image-wrapper">
              <div class="category-tag">{{ getCategoryFromPath(image.name) }}</div>
              <img 
                v-lazy="image.url"
                :alt="formatImageName(image.name)"
              >
              <div class="image-info">
                <p class="image-name">{{ formatImageName(image.name) }}</p>
                <p class="image-date">{{ formatDate(image.last_modified) }}</p>
              </div>
              <div class="image-actions">
                <button @click.stop="showPreview(image)" title="放大">
                  <i class="fas fa-search-plus"></i>
                </button>
                <button @click.stop="copyImageUrl(image)" title="复制链接">
                  <i class="fas fa-link"></i>
                </button>
                <button @click.stop="openEditModal(image)" title="重命名">
                  <i class="fas fa-edit"></i>
                </button>
                <button @click.stop="openDeleteConfirm(image)" title="删除" class="delete-btn">
                  <i class="fas fa-trash-alt"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 无限滚动加载触发器 -->
        <div 
          v-show="!loading && hasMore" 
          class="load-more" 
          ref="loadMoreTrigger"
        >
          <div class="loading-spinner">
            <div class="spinner"></div>
            <span>加载更多...</span>
          </div>
        </div>
      </div>

      <!-- 编辑图片的弹窗 -->
      <div v-if="showEditModal" class="modal-overlay">
        <div class="modal">
          <h3>编辑图片</h3>
          <div class="edit-form">
            <div class="form-group">
              <label>图片名称</label>
              <input 
                v-model="editForm.name" 
                type="text" 
                class="edit-input"
                placeholder="输入新的名称"
              >
            </div>
            <div class="form-group">
              <label>分类</label>
              <select v-model="editForm.category" class="category-select">
                <option v-for="category in allCategories" :key="category" :value="category">
                  {{ category }}
                </option>
              </select>
            </div>
          </div>
          <div class="modal-buttons">
            <button @click="confirmEdit" class="confirm-btn" :disabled="!isEditValid">
              确定
            </button>
            <button @click="cancelEdit" class="cancel-btn">取消</button>
          </div>
        </div>
      </div>

      <!-- 图片预览弹窗 -->
      <div v-if="previewImage" class="modal-overlay preview-modal" @click="closePreview">
        <div class="preview-content" @click.stop>
          <button class="nav-btn prev" @click.stop="navigateImage(-1)" v-if="canNavigatePrev">
            <i class="fas fa-chevron-left"></i>
          </button>
          <img :src="previewImage.url" :alt="formatImageName(previewImage.name)">
          <button class="nav-btn next" @click.stop="navigateImage(1)" v-if="canNavigateNext">
            <i class="fas fa-chevron-right"></i>
          </button>
          <div class="preview-info">
            <span class="preview-name">{{ formatImageName(previewImage.name) }}</span>
            <span class="preview-category">{{ getCategoryFromPath(previewImage.name) }}</span>
          </div>
          <button class="close-btn" @click="closePreview">
            <i class="fas fa-times"></i>
          </button>
        </div>
      </div>

      <!-- 提示消息 -->
      <div v-if="toastVisible" class="toast" :class="toastType">
        {{ toastMessage }}
      </div>

      <!-- 删除确认弹窗 -->
      <div v-if="showDeleteConfirm" class="modal-overlay">
        <div class="modal">
          <h3>确认删除</h3>
          <p>确定要删除这张图片吗？此操作不可恢复。</p>
          <div class="modal-buttons">
            <button @click="confirmDelete" class="confirm-btn delete">确定删除</button>
            <button @click="cancelDelete" class="cancel-btn">取消</button>
          </div>
        </div>
      </div>

      <!-- 添加批量移动的弹窗 -->
      <div v-if="showBatchMoveModal" class="modal-overlay">
        <div class="modal">
          <h3>移动到</h3>
          <div class="form-group">
            <select v-model="targetCategory" class="category-select">
              <option value="">请选择目标分类</option>
              <option v-for="category in allCategories" :key="category" :value="category">
                {{ category }}
              </option>
            </select>
          </div>
          <div class="modal-buttons">
            <button @click="confirmBatchMove" class="confirm-btn" :disabled="!targetCategory">
              确定
            </button>
            <button @click="showBatchMoveModal = false" class="cancel-btn">取消</button>
          </div>
        </div>
      </div>

      <!-- 修改分类管理弹窗 -->
      <div v-if="showCategoryModal" class="modal-overlay">
        <div class="modal category-modal">
          <h3>分类管理</h3>
          <div class="category-list">
            <draggable 
              v-model="allCategories" 
              item-key="id"
              handle=".drag-handle"
              @end="onDragEnd"
            >
              <template #item="{ element }">
                <div class="category-item">
                  <div class="category-info">
                    <span class="category-name">
                      <i class="fas fa-folder"></i>
                      {{ element }}
                    </span>
                  </div>
                  <div class="category-actions">
                    <button class="action-btn drag-handle" title="拖动排序">
                      <i class="fas fa-grip-vertical"></i>
                    </button>
                    <button @click.stop="confirmDeleteCategory(element)" class="action-btn delete" title="删除">
                      <i class="fas fa-trash-alt"></i>
                    </button>
                  </div>
                </div>
              </template>
            </draggable>
          </div>
          <div class="modal-buttons">
            <button @click="closeCategoryModal" class="cancel-btn">关闭</button>
          </div>
        </div>
      </div>

      <!-- 添加统计按钮 -->
      <button class="stats-btn" @click="showStatsModal = true">
        <i class="fas fa-chart-pie"></i>
        统计信息
      </button>

      <!-- 统计信息弹窗 -->
      <div v-if="showStatsModal" class="modal-overlay">
        <div class="modal stats-modal">
          <h3>存储空间统计</h3>
          
          <div class="stats-content">
            <!-- 总体统计 -->
            <div class="stats-section">
              <h4>总体统计</h4>
              <div class="stats-grid">
                <div class="stat-box">
                  <i class="fas fa-hdd"></i>
                  <div class="stat-info">
                    <span class="stat-label">总存储空间</span>
                    <span class="stat-value">{{ stats.total_size_formatted }}</span>
                  </div>
                </div>
                <div class="stat-box">
                  <i class="fas fa-images"></i>
                  <div class="stat-info">
                    <span class="stat-label">总文件数</span>
                    <span class="stat-value">{{ stats.total_files }} 个</span>
                  </div>
                </div>
                <div class="stat-box">
                  <i class="fas fa-cloud-upload-alt"></i>
                  <div class="stat-info">
                    <span class="stat-label">最近30天上传</span>
                    <span class="stat-value">{{ stats.recent_uploads }} 个</span>
                  </div>
                </div>
              </div>
            </div>

            <!-- 分类统计 -->
            <div class="stats-section">
              <h4>分类统计</h4>
              <div class="stats-table">
                <table>
                  <thead>
                    <tr>
                      <th>分类名称</th>
                      <th>文件数量</th>
                      <th>占用空间</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(stat, category) in stats.categories" :key="category">
                      <td>{{ category }}</td>
                      <td>{{ stat.count }} 个</td>
                      <td>{{ stat.size_formatted }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>

            <!-- 文件类型统计 -->
            <div class="stats-section">
              <h4>文件类型统计</h4>
              <div class="stats-table">
                <table>
                  <thead>
                    <tr>
                      <th>文件类型</th>
                      <th>文件数量</th>
                      <th>占用空间</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(stat, type) in stats.file_types" :key="type">
                      <td>{{ type || '无扩展名' }}</td>
                      <td>{{ stat.count }} 个</td>
                      <td>{{ stat.size_formatted }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>

          <div class="modal-buttons">
            <button @click="refreshStats" class="refresh-btn">
              <i class="fas fa-sync-alt"></i>
              刷新
            </button>
            <button @click="showStatsModal = false" class="cancel-btn">关闭</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/utils/axios';
import { defineComponent, ref, onMounted, onUnmounted, watch } from 'vue';
import draggable from 'vuedraggable';
import { formatImageName, getCategoryFromPath } from '@/utils/format';
import { compressImage } from '@/utils/imageLoader';

export default defineComponent({
  name: 'HomeView',
  components: {
    draggable
  },
  setup() {
    // 状态定义
    const images = ref([]);
    const allCategories = ref([]);
    const currentCategory = ref('');
    const searchQuery = ref('');
    const loading = ref(false);
    const hasMore = ref(true);
    const currentPage = ref(1);
    const uploading = ref(false);
    const uploadProgress = ref('');
    const showAddCategory = ref(false);
    const newCategory = ref('');
    const showCategoryModal = ref(false);
    const categoryOrder = ref([]);
    const selectedImages = ref([]);
    const showUploadModal = ref(false);
    const pendingFiles = ref([]);
    const selectedCategory = ref('');
    const showBatchMoveModal = ref(false);
    const targetCategory = ref('');
    const showEditModal = ref(false);
    const editForm = ref({
      name: '',
      category: ''
    });
    const currentEditImage = ref(null);
    const previewImage = ref(null);
    const toastMessage = ref('');
    const toastType = ref('');
    const toastVisible = ref(false);
    const showDeleteConfirm = ref(false);
    const imageToDelete = ref(null);
    const showStatsModal = ref(false);
    const stats = ref({
      total_size: 0,
      total_size_formatted: '0 B',
      total_files: 0,
      recent_uploads: 0,
      categories: {},
      file_types: {}
    });
    const selectedFiles = ref([]);
    const fileInput = ref(null);
    const uploadPercentage = ref(0);
    const categoryToDelete = ref(null);
    const downloading = ref(false);

    // 工具方法
    const formatDate = (dateStr) => {
      return new Date(dateStr).toLocaleString();
    };

    // 显示提示消息
    const toast = (message, type = 'success') => {
      toastMessage.value = message;
      toastType.value = type;
      toastVisible.value = true;
      setTimeout(() => {
        toastVisible.value = false;
      }, 3000);
    };

    // 加载图片
    const loadMore = async () => {
      if (loading.value || !hasMore.value) return;

      try {
        loading.value = true;
        const response = await axios.get('/images', {
          params: {
            page: currentPage.value,
            pageSize: 20,
            category: currentCategory.value,
            search: searchQuery.value
          }
        });

        if (response.data.success) {
          const { images: newImages, hasMore: more } = response.data.data;
          const processedImages = newImages.map(img => ({
            ...img,
            url: img.url.startsWith('http') ? img.url : `http://124.71.1.69:9000/image/${img.name}`
          }));
          
          images.value.push(...processedImages);
          hasMore.value = more;
          currentPage.value += 1;
        }
      } catch (error) {
        console.error('获取图片列表失败:', error);
      } finally {
        loading.value = false;
      }
    };

    // 重置加载状态
    const resetLoading = () => {
      currentPage.value = 1;
      hasMore.value = true;
      images.value = [];
      loadMore();
    };

    // 更新分类列表
    const updateCategories = async () => {
      try {
        const response = await axios.get('/images', {
          params: { page: 1, pageSize: 1000 }
        });
        
        if (response.data.success) {
          const categorySet = new Set();
          response.data.data.images.forEach(img => {
            const category = getCategoryFromPath(img.name);
            if (category) {
              categorySet.add(category);
            }
          });
          allCategories.value = Array.from(categorySet);
        }
      } catch (error) {
        console.error('获取分类列表失败:', error);
      }
    };

    // 添加分类方法
    const addCategory = async () => {
      if (!newCategory.value.trim()) {
        toast('分类名称不能为空', 'error');
        return;
      }

      if (allCategories.value.includes(newCategory.value.trim())) {
        toast('分类已存在', 'error');
        return;
      }

      try {
        const response = await axios.post('/categories', {
          name: newCategory.value.trim()
        });

        if (response.data.success) {
          toast('添加分类成功');
          newCategory.value = '';
          showAddCategory.value = false;
          await updateCategories();
        } else {
          toast(response.data.message || '添加分类失败', 'error');
        }
      } catch (error) {
        console.error('添加分类失败:', error);
        toast('添加分类失败', 'error');
      }
    };

    // 更新分类顺序
    const updateCategoryOrder = async () => {
      try {
        const response = await axios.post('/categories/order', {
          categories: allCategories.value
        });
        
        if (response.data.success) {
          toast('分类顺序更新成功');
        } else {
          toast(response.data.message || '更新分类顺序失败', 'error');
        }
      } catch (error) {
        console.error('更新分类顺序失败:', error);
        toast('更新分类顺序失败', 'error');
      }
    };

    // 处理拖拽结束
    const onDragEnd = async () => {
      await updateCategoryOrder();
    };

    // 添加滚动监听
    const handleScroll = () => {
      const scrollHeight = document.documentElement.scrollHeight;
      const scrollTop = document.documentElement.scrollTop;
      const clientHeight = document.documentElement.clientHeight;
      
      // 当滚动到距离底部100px时加载更多
      if (scrollHeight - scrollTop - clientHeight < 100) {
        loadMore();
      }
    };

    // 在组件挂载时添加滚动监听
    onMounted(() => {
      updateCategories();
      loadMore();
      getCategoryOrder();
      window.addEventListener('scroll', handleScroll);
    });

    // 在组件卸载时移除滚动监听
    onUnmounted(() => {
      window.removeEventListener('scroll', handleScroll);
    });

    // 监听搜索和分类变化
    watch([searchQuery, currentCategory], () => {
      resetLoading();
    });

    // 处理文件选择
    const handleFileSelect = (event) => {
      const files = event.target.files;
      if (files) {
        selectedFiles.value = Array.from(files);
        showUploadModal.value = true;  // 显示上传模态框
      }
    };

    // 取消上传
    const cancelUpload = () => {
      showUploadModal.value = false;
      selectedFiles.value = [];
      selectedCategory.value = '';
      if (fileInput.value) {
        fileInput.value.value = '';  // 清空文件输入
      }
    };

    // 上传图片
    const confirmUpload = async () => {
      if (!selectedFiles.value.length || !selectedCategory.value) {
        toast('请选择文件和分类', 'error');
        return;
      }
      
      uploading.value = true;
      uploadProgress.value = '准备上传...';
      let successCount = 0;
      const totalFiles = selectedFiles.value.length;
      
      try {
        for (let i = 0; i < selectedFiles.value.length; i++) {
          const file = selectedFiles.value[i];
          uploadProgress.value = `压缩第 ${i + 1}/${totalFiles} 个文件...`;
          
          try {
            // 压缩图片
            const compressedFile = await compressImage(file, {
              maxSizeMB: 1,         // 最大1MB
              maxWidthOrHeight: 1920, // 最大宽度或高度1920px
              useWebWorker: true,
              onProgress: (percent) => {
                uploadPercentage.value = Math.round(percent * 0.5); // 压缩过程占50%进度
              }
            });

            uploadProgress.value = `上传第 ${i + 1}/${totalFiles} 个文件...`;
            
            const formData = new FormData();
            formData.append('image', compressedFile, file.name); // 使用原始文件名
            formData.append('category', selectedCategory.value);
            
            const response = await axios.post('/upload', formData, {
              headers: {
                'Content-Type': 'multipart/form-data'
              },
              onUploadProgress: (progressEvent) => {
                // 上传过程占另外50%进度
                const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
                uploadPercentage.value = 50 + Math.round(percentCompleted * 0.5);
              }
            });

            if (response.data.success) {
              successCount++;
              // 立即添加到图片列表
              images.value.unshift({
                url: response.data.data.url,
                name: response.data.data.name,
                last_modified: response.data.data.last_modified
              });
            }
          } catch (error) {
            console.error(`文件 ${file.name} 处理失败:`, error);
          }
        }

        if (successCount === totalFiles) {
          toast('上传成功');
          showUploadModal.value = false;
        } else if (successCount === 0) {
          toast('上传失败', 'error');
        } else {
          toast(`部分文件上传成功 (${successCount}/${totalFiles})`, 'warning');
        }

      } catch (error) {
        console.error('上传过程出错:', error);
        toast('上传过程出错', 'error');
      } finally {
        uploading.value = false;
        uploadProgress.value = '';
        uploadPercentage.value = 0;
        selectedFiles.value = [];
        if (fileInput.value) {
          fileInput.value.value = '';
        }
      }
    };

    // 打开删除确认弹窗
    const openDeleteConfirm = (image) => {
      imageToDelete.value = image;
      showDeleteConfirm.value = true;
    };

    // 取消删除
    const cancelDelete = () => {
      showDeleteConfirm.value = false;
      imageToDelete.value = null;
    };

    // 确认删除
    const confirmDelete = async () => {
      if (!imageToDelete.value) return;

      try {
        const response = await axios.delete(`/images/${imageToDelete.value.name}`);
        if (response.data.success) {
          toast('删除成功');
          // 从列表中移除
          const index = images.value.findIndex(img => img.name === imageToDelete.value.name);
          if (index > -1) {
            images.value.splice(index, 1);
          }
          // 关闭弹窗
          showDeleteConfirm.value = false;
          imageToDelete.value = null;
        } else {
          toast(response.data.message || '删除失败', 'error');
        }
      } catch (error) {
        console.error('删除失败:', error);
        toast('删除失败', 'error');
      }
    };

    // 获取统计信息
    const fetchStats = async () => {
      try {
        const response = await axios.get('/stats');
        if (response.data.success) {
          stats.value = response.data.data;
        }
      } catch (error) {
        console.error('获取统计信息失败:', error);
        toast('获取统计信息失败', 'error');
      }
    };

    // 刷新统计信息
    const refreshStats = () => {
      fetchStats();
      toast('统计信息已更新');
    };

    const confirmEdit = async () => {
      if (!currentEditImage.value || !editForm.value.name || !editForm.value.category) {
        toast('请填写完整信息', 'error');
        return;
      }

      try {
        const response = await axios.post('/rename', {
          oldName: currentEditImage.value.name,
          newName: editForm.value.name,
          category: editForm.value.category
        });

        if (response.data.success) {
          // 更新图片列表中的对应项
          const index = images.value.findIndex(img => img.name === currentEditImage.value.name);
          if (index !== -1) {
            images.value[index] = {
              ...images.value[index],
              ...response.data.data
            };
          }

          toast('编辑成功');
          showEditModal.value = false;
        } else {
          toast(response.data.message || '编辑失败', 'error');
        }
      } catch (error) {
        console.error('编辑失败:', error);
        toast(error.response?.data?.message || '编辑失败', 'error');
      }
    };

    // 选择/取消选择图片
    const toggleSelect = (image) => {
      const index = selectedImages.value.findIndex(img => img.name === image.name);
      if (index === -1) {
        selectedImages.value.push(image);
      } else {
        selectedImages.value.splice(index, 1);
      }
    };

    // 检查图片是否被选中
    const isSelected = (image) => {
      return selectedImages.value.some(img => img.name === image.name);
    };

    // 清除所有选择
    const clearSelection = () => {
      selectedImages.value = [];
    };

    // 批量删除
    const confirmBatchDelete = async () => {
      if (!confirm(`确定要删除选中的 ${selectedImages.value.length} 张图片吗？`)) {
        return;
      }

      try {
        const response = await axios.post('/batch/delete', {
          names: selectedImages.value.map(img => img.name)
        });

        if (response.data.success) {
          // 从列表中移除已删除的图片
          selectedImages.value.forEach(img => {
            const index = images.value.findIndex(i => i.name === img.name);
            if (index !== -1) {
              images.value.splice(index, 1);
            }
          });
          
          toast('批量删除成功');
          selectedImages.value = [];
        } else {
          toast(response.data.message || '批量删除失败', 'error');
        }
      } catch (error) {
        console.error('批量删除失败:', error);
        toast('批量删除失败', 'error');
      }
    };

    // 批量移动
    const confirmBatchMove = async () => {
      if (!targetCategory.value) {
        toast('请选择目标分类', 'error');
        return;
      }

      try {
        const response = await axios.post('/batch/move', {
          names: selectedImages.value.map(img => img.name),
          targetCategory: targetCategory.value
        });

        if (response.data.success) {
          toast('移动成功');
          showBatchMoveModal.value = false;
          selectedImages.value = [];
          resetLoading();
        } else {
          toast(response.data.message || '移动失败', 'error');
        }
      } catch (error) {
        console.error('移动失败:', error);
        toast('移动失败', 'error');
      }
    };

    // 删除分类
    const confirmDeleteCategory = async (category) => {
      if (!confirm(`确定要删除分类"${category}"吗？该分类下的所有图片都会被删除！`)) {
        return;
      }

      try {
        const response = await axios.delete(`/categories/${category}`);
        
        if (response.data.success) {
          toast('删除分类成功');
          // 从分类列表中移除
          const index = allCategories.value.indexOf(category);
          if (index > -1) {
            allCategories.value.splice(index, 1);
          }
          // 如果当前显示的是被删除的分类，则切换到全部
          if (currentCategory.value === category) {
            currentCategory.value = '';
          }
          // 关闭分类管理弹窗
          showCategoryModal.value = false;
        } else {
          toast(response.data.message || '删除分类失败', 'error');
        }
      } catch (error) {
        console.error('删除分类失败:', error);
        toast('删除分类失败', 'error');
      }
    };

    // 关闭分类管理弹窗
    const closeCategoryModal = () => {
      showCategoryModal.value = false;
    };

    // 批量下载
    const batchDownload = async () => {
      if (!selectedImages.value.length) {
        toast('请选择要下载的图片', 'error');
        return;
      }

      downloading.value = true;
      try {
        const token = localStorage.getItem('token');
        const response = await axios.post('/batch/download', {
          names: selectedImages.value.map(img => img.name)
        }, {
          responseType: 'blob',
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        // 检查响应类型
        const contentType = response.headers['content-type'];
        if (contentType === 'application/json') {
          // 如果返回的是 JSON（可能是错误信息）
          const reader = new FileReader();
          reader.onload = () => {
            const error = JSON.parse(reader.result);
            toast(error.message || '下载失败', 'error');
          };
          reader.readAsText(response.data);
          return;
        }

        // 创建下载链接
        const url = window.URL.createObjectURL(new Blob([response.data], { 
          type: 'application/zip' 
        }));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'images.zip');
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);

        toast('下载成功');
      } catch (error) {
        console.error('下载失败:', error);
        if (error.response?.data instanceof Blob) {
          // 如果错误响应是 Blob，尝试读取错误信息
          const reader = new FileReader();
          reader.onload = () => {
            try {
              const errorData = JSON.parse(reader.result);
              toast(errorData.message || '下载失败', 'error');
            } catch (e) {
              toast('下载失败', 'error');
            }
          };
          reader.readAsText(error.response.data);
        } else {
          toast(error.response?.data?.message || '下载失败', 'error');
        }
      } finally {
        downloading.value = false;
      }
    };

    // 获取分类顺序
    const getCategoryOrder = async () => {
      try {
        const response = await axios.get('/categories/order');
        if (response.data.success && response.data.categories) {
          allCategories.value = response.data.categories;
        }
      } catch (error) {
        console.error('获取分类顺序失败:', error);
      }
    };

    return {
      // 状态
      images,
      allCategories,
      currentCategory,
      searchQuery,
      loading,
      hasMore,
      currentPage,
      uploading,
      uploadProgress,
      showAddCategory,
      newCategory,
      showCategoryModal,
      categoryOrder,
      selectedImages,
      showUploadModal,
      pendingFiles,
      selectedCategory,
      showBatchMoveModal,
      targetCategory,
      showEditModal,
      editForm,
      currentEditImage,
      previewImage,
      toastMessage,
      toastType,
      toastVisible,
      showDeleteConfirm,
      imageToDelete,
      showStatsModal,
      stats,
      selectedFiles,
      fileInput,
      uploadPercentage,
      categoryToDelete,
      downloading,

      // 方法
      loadMore,
      resetLoading,
      getCategoryFromPath,
      formatDate,
      updateCategories,
      toast,
      addCategory,
      onDragEnd,
      handleFileSelect,
      cancelUpload,
      confirmUpload,
      openDeleteConfirm,
      cancelDelete,
      confirmDelete,
      fetchStats,
      refreshStats,
      formatImageName,
      confirmEdit,
      confirmBatchDelete,
      toggleSelect,
      isSelected,
      clearSelection,
      batchDownload,
      confirmBatchMove,
      confirmDeleteCategory,
      closeCategoryModal,
      getCategoryOrder
    };
  },
  computed: {
    filteredImages() {
      let filtered = [...this.images];
      
      if (this.currentCategory) {
        filtered = filtered.filter(image => 
          this.getCategoryFromPath(image.name) === this.currentCategory
        );
      }
      
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase();
        filtered = filtered.filter(image => 
          image.name.toLowerCase().includes(query)
        );
      }
      
      return filtered;
    },
    sortedCategories() {
      if (this.categoryOrder.length === 0) {
        return [...this.allCategories].sort();
      }
      return [...this.allCategories].sort((a, b) => {
        return this.categoryOrder.indexOf(a) - this.categoryOrder.indexOf(b);
      });
    },
    isEditValid() {
      return this.editForm.name && this.editForm.category;
    },
    currentPreviewIndex() {
      return this.filteredImages.findIndex(img => img.name === this.previewImage?.name);
    },
    canNavigatePrev() {
      return this.currentPreviewIndex > 0;
    },
    canNavigateNext() {
      return this.currentPreviewIndex < this.filteredImages.length - 1;
    }
  },
  methods: {
    triggerUpload() {
      this.$refs.fileInput.click();
    },
    openEditModal(image) {
      const [category, ...nameParts] = image.name.split('/');
      const name = nameParts.join('/').split('_')[0]; // 去掉时间戳部分
      
      this.editForm = {
        name: name,
        category: category,
        url: image.url
      };
      this.currentEditImage = image;
      this.showEditModal = true;
    },
    cancelEdit() {
      this.showEditModal = false;
      this.editForm = {
        name: '',
        category: '',
        url: ''
      };
    },
    async copyImageUrl(image) {
      try {
        await navigator.clipboard.writeText(image.url);
        this.toast('链接已复制到剪贴板');
      } catch (err) {
        console.error('复制失败:', err);
        this.toast('复制失败', 'error');
      }
    },
    showPreview(image) {
      this.previewImage = image;
    },
    closePreview() {
      this.previewImage = null;
    }
  }
})
</script>

