/**
 * 格式化图片名称
 * 从完整路径中提取图片名称，并去除时间戳
 * 例如: "风景/图片_20240315_123456.jpg" -> "图片"
 */
export const formatImageName = (fullPath) => {
  if (!fullPath) return '';
  
  // 获取文件名（去掉路径）
  const fileName = fullPath.split('/').pop();
  
  // 去掉扩展名
  const nameWithoutExt = fileName.split('.')[0];
  
  // 查找最后一个时间戳的位置（格式：_YYYYMMDD_HHMMSS）
  const timestampMatch = nameWithoutExt.match(/_\d{8}_\d{6}$/);
  if (timestampMatch) {
    // 返回时间戳之前的部分
    return nameWithoutExt.slice(0, timestampMatch.index);
  }
  
  return nameWithoutExt; // 如果没有找到时间戳，返回不带扩展名的文件名
};

/**
 * 获取文件扩展名
 */
export const getFileExtension = (fileName) => {
  return fileName.split('.').pop().toLowerCase();
};

/**
 * 从路径中获取分类名
 */
export const getCategoryFromPath = (path) => {
  const parts = path.split('/');
  return parts.length > 1 ? parts[0] : '未分类';
}; 